@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
*{
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif;
}
:root{
    --primary-color:#a3edf6;
    --secondary-color:#61d0f5;
}
body{
    background-color: var(--primary-color);
}
.header{
    background-color: var(--secondary-color);
    height: 100px;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 30px;
}

.search-btn{
    display: flex;
}
.inputText{
    outline: none;
    border: white;
    border-radius: 2rem 0 0 2rem;
    padding: 0.3rem 1rem;
    font-size: 1rem;
    color: rgb(13, 56, 74);
    background-color: var(--primary-color);
    box-shadow: inset 0 5px 10px var(--secondary-color);
}
.inputText::placeholder{
    color: rgb(5, 44, 45);
    font-weight: 500;
}
.search-btn button{
    background-color:#35bbe8;
    box-shadow: inset 0 5px 10px var(--primary-color);
    padding: 0.2rem 0.5rem;
    border-radius: 0 2rem 2rem 0;
    color: white;
    font-size: 1rem;
    outline: none;
    border: 1px solid var(--primary-color);
}
.container{
    margin: auto;
    width: 80%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.movie{
    box-shadow: 0 5px 10px rgba(0,0,0,0.5);
    width: 250px;
    background-color: var(--secondary-color);
    margin: 1rem;
    border-radius: 5px;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
}
.movie .poster{
    width: 100%;
    height: 250px;
}
.movie-details{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.movie-details .box{
    width: 100%;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.movie-details .title{
    width: 160px;
    font-size: 15px;
    text-align: center;
}
.movie-details .overview{
    position: absolute;
    left: 0;
    bottom: 0;
    max-height: 100%;
    background-color: var(--secondary-color);
    opacity: 0.9;
    color: white;
    box-sizing: border-box;
    padding: 1rem;
    transition: all 0.4s ease-in-out;
    overflow-y: auto;
    transform: translateY(101%);
}
.movie-details:hover .overview{
    transform: translateY(0%);
}

.movie-details p{
    color: blue;
    font-weight: 500;
}

.movie-details h5{
    color: rgb(146, 9, 35);
    font-weight: 600;
}

.movie-details span{
    color: rgb(16, 16, 36);
    font-weight: 600;
}
.rating{
    background-color: var(--primary-color);
    width: 35px;
    height: 35px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    border: 1px solid var(--primary-color);
    box-shadow: inset 0 5px 5px rgba(0,0,0,0.5);
    position: absolute;
    right: 5px;
    bottom: 5px;
}
@media (max-width:900px){
    .header{
        background-color: var(--secondary-color);
        height: 180px;
    }
    /* nav ul{
        display: flex;
        flex-direction: column;
    }
    nav ul li{
        padding-top: 10px;
    } */
    .search-btn{
        width: 200px;
        margin-right: 100px;
    }
}
